// Learn life-cycle callbacks:
//  - https://docs.cocos.com/creator/manual/en/scripting/life-cycle-callbacks.html

cc.Class({
    extends: cc.Component,

    properties: {
        highscoreText: cc.Label,
        clickSound: cc.AudioClip
    },

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {},

    start () {
        let highscore = cc.sys.localStorage.getItem('highscore') || 0;
        this.highscoreText.string = `-HIGHSCORE: ${highscore}-`;
        this.highscoreText.node.active = highscore > 0;
    },

    onPlayButtonClicked () {
        cc.director.loadScene("ingame");
        cc.audioEngine.play(this.clickSound, false, 1);
    }

    // update (dt) {},
});
