cc.Class({
    extends: cc.Component,

    properties: {
        overlay: cc.Node,
        frame: cc.Node,
        scoreText: cc.Label,
        highScoreText: cc.Label,

        clickSound: cc.AudioClip
    },

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {},

    start () {

    },

    show (score) {
        this.overlay.active = true;
        this.frame.active = true;
        this.scoreText.string = 'You scored: ' + score;

        let highscore = cc.sys.localStorage.getItem('highscore') || 0;
        let isHighscore = score > highscore;
        if (isHighscore) cc.sys.localStorage.setItem('highscore', score);
        this.highScoreText.node.active = isHighscore;
        
        this.frame.setScale(0.3);
        cc.tween(this.frame)
            .to(0.4, {scale: {value: 1, easing: 'sineInOut'}})
            .start();
    },

    hide (immediate) {
        this.overlay.active = false;
        if (immediate) {
            this.frame.active = false;
            this.frame.setScale(0);
        }else {
            cc.tween(this.frame)
            .to(0.4, {scale: {value: 0, easing: 'sineInOut'}})
            .call(() => {
                this.frame.active = false;
            })
            .start();
        }
    },

    onBackToMenuClicked () {
        cc.audioEngine.play(this.clickSound, false, 1);
        cc.director.loadScene("landing");
    },

    onRestartClicked () {
        cc.audioEngine.play(this.clickSound, false, 1);
        this.node.emit('restart');
    }

    // update (dt) {},
});
